import React, { useState, useEffect } from 'react';

const DietPlanGenerator = ({ userInfo }) => {
  const [dietPlan, setDietPlan] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('overview');

  useEffect(() => {
    if (userInfo) {
      generateDietPlan(userInfo);
    }
  }, [userInfo]);

  const calculateBMR = (userInfo) => {
    // Harris-Benedict denklemi
    const { gender, weight, height, age } = userInfo;
    let bmr = 0;
    
    if (gender === 'erkek') {
      bmr = 88.362 + (13.397 * parseFloat(weight)) + (4.799 * parseFloat(height)) - (5.677 * parseFloat(age));
    } else {
      bmr = 447.593 + (9.247 * parseFloat(weight)) + (3.098 * parseFloat(height)) - (4.330 * parseFloat(age));
    }
    
    return Math.round(bmr);
  };

  const calculateTDEE = (bmr, activityLevel) => {
    // Aktivite arpanlar
    const activityMultipliers = {
      sedanter: 1.2,
      hafif_aktif: 1.375,
      orta_aktif: 1.55,
      cok_aktif: 1.725,
      ekstra_aktif: 1.9
    };
    
    return Math.round(bmr * activityMultipliers[activityLevel]);
  };

  const calculateTargetCalories = (tdee, dietGoal) => {
    // Hedeflere gre kalori hesaplama
    let targetCalories = tdee;
    
    switch (dietGoal) {
      case 'kilo_verme':
        targetCalories = Math.round(tdee * 0.8); // %20 kalori a
        break;
      case 'kilo_alma':
        targetCalories = Math.round(tdee * 1.15); // %15 kalori fazlas
        break;
      case 'kas_kazanma':
        targetCalories = Math.round(tdee * 1.1); // %10 kalori fazlas
        break;
      default:
        targetCalories = tdee; // Kilo koruma
    }
    
    // Minumum kalori snr (salk asndan)
    const minCalories = userInfo.gender === 'erkek' ? 1500 : 1200;
    return Math.max(targetCalories, minCalories);
  };

  const calculateWaterIntake = (weight) => {
    // Kilo bana 30ml su hesab (genel kural)
    const minWater = Math.round((parseFloat(weight) * 30) / 1000, 1);
    // Kilo bana 40ml su hesab (aktif kiiler iin)
    const maxWater = Math.round((parseFloat(weight) * 40) / 1000, 1);
    
    return {
      min: minWater,
      max: maxWater,
      cups: Math.round((minWater + maxWater) / 2 * 4) // 250ml'lik bardak says
    };
  };

  const generateMacroDistribution = (targetCalories, dietGoal) => {
    let proteinPercentage, carbPercentage, fatPercentage;
    
    // Hedeflere gre makro dalm
    switch (dietGoal) {
      case 'kilo_verme':
        proteinPercentage = 0.35; // %35
        fatPercentage = 0.30; // %30
        carbPercentage = 0.35; // %35
        break;
      case 'kilo_alma':
        proteinPercentage = 0.25; // %25
        fatPercentage = 0.25; // %25
        carbPercentage = 0.50; // %50
        break;
      case 'kas_kazanma':
        proteinPercentage = 0.30; // %30
        fatPercentage = 0.25; // %25
        carbPercentage = 0.45; // %45
        break;
      default:
        proteinPercentage = 0.20; // %20
        fatPercentage = 0.30; // %30
        carbPercentage = 0.50; // %50
    }
    
    // Gram hesaplamalar (1g protein/karbonhidrat = 4 kalori, 1g ya = 9 kalori)
    const proteinCalories = targetCalories * proteinPercentage;
    const carbCalories = targetCalories * carbPercentage;
    const fatCalories = targetCalories * fatPercentage;
    
    const proteinGrams = Math.round(proteinCalories / 4);
    const carbGrams = Math.round(carbCalories / 4);
    const fatGrams = Math.round(fatCalories / 9);
    
    return {
      protein: {
        percentage: Math.round(proteinPercentage * 100),
        grams: proteinGrams,
        calories: Math.round(proteinCalories)
      },
      carbs: {
        percentage: Math.round(carbPercentage * 100),
        grams: carbGrams,
        calories: Math.round(carbCalories)
      },
      fat: {
        percentage: Math.round(fatPercentage * 100),
        grams: fatGrams,
        calories: Math.round(fatCalories)
      }
    };
  };

  const generateTurkishMealPlan = (targetCalories, macros, mealCount) => {
    // n saysna gre kalori dalm
    const mealDistribution = {
      '3': { breakfast: 0.25, lunch: 0.40, dinner: 0.35 },
      '4': { breakfast: 0.20, lunch: 0.35, dinner: 0.30, snack1: 0.15 },
      '5': { breakfast: 0.20, lunch: 0.30, dinner: 0.25, snack1: 0.15, snack2: 0.10 },
      '6': { breakfast: 0.20, lunch: 0.25, dinner: 0.25, snack1: 0.10, snack2: 0.10, snack3: 0.10 }
    };
    
    // Trk mutfana uygun yemek nerileri
    const turkishFoods = {
      breakfast: [
        {
          name: 'Kahvalt 1',
          description: '2 dilim tam tahll ekmek, 2 yumurta, 50g lor peyniri, 4-5 adet zeytin, 1 domates, 1 salatalk, 1 tatl ka bal, bitki ay',
          calories: 400
        },
        {
          name: 'Kahvalt 2',
          description: '1 kase yulaf ezmesi (40g), 1 su barda yarm yal st, 1 adet muz, 1 tatl ka tarn, 10g ceviz ii',
          calories: 380
        },
        {
          name: 'Kahvalt 3',
          description: 'Menemen (2 yumurta, 1 domates, 1 biber), 1 dilim tam tahll ekmek, 30g beyaz peynir, 5 adet zeytin, bitki ay',
          calories: 420
        }
      ],
      lunch: [
        {
          name: 'le Yemei 1',
          description: '120g zgara tavuk, 1 kase sebzeli bulgur pilav, mevsim salata (zeytinyal, limon), 1 kase yourt',
          calories: 550
        },
        {
          name: 'le Yemei 2',
          description: '100g etli sebze yemei, 1 porsiyon mercimek orbas, 1 dilim tam tahll ekmek, 1 kase yourt',
          calories: 500
        },
        {
          name: 'le Yemei 3',
          description: '1 porsiyon kuru fasulye, 1/2 porsiyon bulgur pilav, 1 kase cack, yeil salata',
          calories: 520
        }
      ],
      dinner: [
        {
          name: 'Akam Yemei 1',
          description: '150g zgara balk, 1 orta boy halanm patates, mevsim salata (zeytinyal, limon)',
          calories: 450
        },
        {
          name: 'Akam Yemei 2',
          description: '100g kfte (az yal), 1/2 porsiyon bulgur pilav, 1 kase yourt, mevsim salata',
          calories: 480
        },
        {
          name: 'Akam Yemei 3',
          description: '1 porsiyon sebzeli tavuk sote, 1 kase oban salata, 1 dilim tam tahll ekmek',
          calories: 430
        }
      ],
      snacks: [
        {
          name: 'Ara n 1',
          description: '1 adet orta boy meyve (elma, armut veya portakal) + 30g tuzsuz badem',
          calories: 200
        },
        {
          name: 'Ara n 2',
          description: '1 kase yarm yal yourt + 1 tatl ka bal + 1 yemek ka yulaf',
          calories: 180
        },
        {
          name: 'Ara n 3',
          description: '1 avu kark kuruyemi (badem, ceviz, fndk)',
          calories: 170
        },
        {
          name: 'Ara n 4',
          description: '2 adet tam tahll biskvi + 1 bardak yarm yal ayran',
          calories: 150
        },
        {
          name: 'Ara n 5',
          description: '1 kk kase mevsim meyveleri + 30g ekirdeksiz kuru zm',
          calories: 160
        }
      ]
    };
    
    // n plan olutur
    const mealPlan = {};
    const distributionPlan = mealDistribution[mealCount.toString()];
    
    Object.keys(distributionPlan).forEach(meal => {
      const mealCalories = Math.round(targetCalories * distributionPlan[meal]);
      
      if (meal === 'breakfast') {
        // Rastgele bir kahvalt se
        const selectedMeal = turkishFoods.breakfast[Math.floor(Math.random() * turkishFoods.breakfast.length)];
        mealPlan[meal] = {
          ...selectedMeal,
          targetCalories: mealCalories
        };
      } else if (meal === 'lunch') {
        // Rastgele bir le yemei se
        const selectedMeal = turkishFoods.lunch[Math.floor(Math.random() * turkishFoods.lunch.length)];
        mealPlan[meal] = {
          ...selectedMeal,
          targetCalories: mealCalories
        };
      } else if (meal === 'dinner') {
        // Rastgele bir akam yemei se
        const selectedMeal = turkishFoods.dinner[Math.floor(Math.random() * turkishFoods.dinner.length)];
        mealPlan[meal] = {
          ...selectedMeal,
          targetCalories: mealCalories
        };
      } else {
        // Ara n
        const snackIndex = Math.floor(Math.random() * turkishFoods.snacks.length);
        mealPlan[meal] = {
          ...turkishFoods.snacks[snackIndex],
          targetCalories: mealCalories
        };
      }
    });
    
    return mealPlan;
  };

  const generateDietPlan = (userInfo) => {
    setIsLoading(true);
    try {
      // BMR (Bazal Metabolizma Hz) Hesaplama
      const bmr = calculateBMR(userInfo);
      
      // TDEE (Toplam Gnlk Enerji Harcamas) Hesaplama
      const tdee = calculateTDEE(bmr, userInfo.activityLevel);
      
      // Hedef Kalori Hesaplama
      const targetCalories = calculateTargetCalories(tdee, userInfo.dietGoal);
      
      // Makro Besin Dalm
      const macroDistribution = generateMacroDistribution(targetCalories, userInfo.dietGoal);
      
      // n Plan
      const mealPlan = generateTurkishMealPlan(targetCalories, macroDistribution, userInfo.mealCount || 3);
      
      // Diyet Plan Objesi
      const generatedPlan = {
        stats: {
          bmr: bmr,
          tdee: tdee,
          targetCalories: targetCalories
        },
        macros: macroDistribution,
        mealPlan: mealPlan,
        waterIntake: calculateWaterIntake(userInfo.weight)
      };
      
      setDietPlan(generatedPlan);
    } catch (error) {
      console.error("Diyet plan oluturulurken hata:", error);
    } finally {
      setIsLoading(false);
    }
  };

  const formatDate = (date) => {
    if (!date) return '';
    return date.toLocaleDateString('tr-TR', { 
      year: 'numeric', 
      month: 'long', 
      day: 'numeric' 
    });
  };

  if (isLoading) {
    return (
      <div className="text-center py-10">
        <div className="inline-block animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-diet-green"></div>
        <p className="mt-3 text-gray-600">Diyet plannz oluturuluyor...</p>
      </div>
    );
  }

  if (!dietPlan) {
    return (
      <div className="text-center py-10">
        <p className="text-red-500">Diyet plan oluturulamad. Ltfen daha sonra tekrar deneyin.</p>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg shadow-lg p-6">
      <h2 className="text-2xl font-bold text-diet-green mb-6 text-center">Kiiselletirilmi Beslenme Plannz</h2>
      
      <div className="mb-6">
        <div className="flex justify-center space-x-4 border-b">
          <button 
            className={`py-2 px-4 font-medium ${activeTab === 'overview' ? 'text-diet-green border-b-2 border-diet-green' : 'text-gray-500'}`}
            onClick={() => setActiveTab('overview')}
          >
            Genel Bilgiler
          </button>
          <button 
            className={`py-2 px-4 font-medium ${activeTab === 'meals' ? 'text-diet-green border-b-2 border-diet-green' : 'text-gray-500'}`}
            onClick={() => setActiveTab('meals')}
          >
            Beslenme Plan
          </button>
          <button 
            className={`py-2 px-4 font-medium ${activeTab === 'tips' ? 'text-diet-green border-b-2 border-diet-green' : 'text-gray-500'}`}
            onClick={() => setActiveTab('tips')}
          >
            Beslenme nerileri
          </button>
        </div>
      </div>
      
      {activeTab === 'overview' && (
        <div>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
            <div className="bg-gray-50 p-4 rounded-lg">
              <h3 className="font-bold text-diet-green text-lg mb-2">Gnlk Kalori htiyac</h3>
              <p className="text-2xl font-bold text-gray-800">{dietPlan.stats.targetCalories} kcal</p>
              <div className="text-sm text-gray-500 mt-1">
                <p>Bazal Metabolizma: {dietPlan.stats.bmr} kcal</p>
                <p>Toplam Enerji htiyac: {dietPlan.stats.tdee} kcal</p>
              </div>
            </div>
            
            <div className="bg-gray-50 p-4 rounded-lg">
              <h3 className="font-bold text-diet-green text-lg mb-2">Su Tketimi</h3>
              <p className="text-2xl font-bold text-gray-800">{dietPlan.waterIntake.min}-{dietPlan.waterIntake.max} litre/gn</p>
              <div className="text-sm text-gray-500 mt-1">
                <p>Yaklak: {dietPlan.waterIntake.cups} bardak su</p>
              </div>
            </div>
            
            <div className="bg-gray-50 p-4 rounded-lg">
              <h3 className="font-bold text-diet-green text-lg mb-2">Makro Besin Dalm</h3>
              <div className="flex justify-between items-center">
                <div className="text-center">
                  <p className="text-sm text-gray-500">Protein</p>
                  <p className="font-bold text-gray-800">{dietPlan.macros.protein.grams}g</p>
                  <p className="text-xs text-gray-500">%{dietPlan.macros.protein.percentage}</p>
                </div>
                <div className="text-center">
                  <p className="text-sm text-gray-500">Karbonhidrat</p>
                  <p className="font-bold text-gray-800">{dietPlan.macros.carbs.grams}g</p>
                  <p className="text-xs text-gray-500">%{dietPlan.macros.carbs.percentage}</p>
                </div>
                <div className="text-center">
                  <p className="text-sm text-gray-500">Ya</p>
                  <p className="font-bold text-gray-800">{dietPlan.macros.fat.grams}g</p>
                  <p className="text-xs text-gray-500">%{dietPlan.macros.fat.percentage}</p>
                </div>
              </div>
            </div>
          </div>
          
          <div className="mb-6">
            <h3 className="font-bold text-diet-green text-xl mb-4">Beslenme nerileri</h3>
            <div className="bg-gray-50 p-4 rounded-lg">
              <ul className="list-disc list-inside space-y-2">
                <li>Gnde en az 2-3 litre su imeye zen gsterin.</li>
                <li>nlerinizi dzenli saatlerde tketin ve n atlamayn.</li>
                <li>Besinleri iyice ineyerek yava yava tketin.</li>
                <li>Yemeklerinizi piirirken kzartma yerine zgara, halama, frnda piirme gibi salkl piirme yntemlerini tercih edin.</li>
                <li>Tuz tketiminizi snrlandrn ve yemeklerinizde baharat kullanmay tercih edin.</li>
                <li>Rafine eker tketimini azaltn, tatl ihtiyacnz iin meyve gibi doal tatllar tercih edin.</li>
                <li>Haftada en az 3-4 kez 30-45 dakikalk egzersiz yapn.</li>
              </ul>
            </div>
          </div>
        </div>
      )}
      
      {activeTab === 'meals' && (
        <div>
          <h3 className="font-bold text-diet-green text-xl mb-4">Gnlk Beslenme Plan</h3>
          
          <div className="space-y-6">
            {Object.keys(dietPlan.mealPlan).map(meal => {
              const mealInfo = dietPlan.mealPlan[meal];
              let mealTitle = '';
              
              switch(meal) {
                case 'breakfast':
                  mealTitle = 'Kahvalt';
                  break;
                case 'lunch':
                  mealTitle = 'le Yemei';
                  break;
                case 'dinner':
                  mealTitle = 'Akam Yemei';
                  break;
                default:
                  const snackNumber = meal.replace('snack', '');
                  mealTitle = `Ara n ${snackNumber}`;
              }
              
              return (
                <div key={meal} className="bg-gray-50 p-4 rounded-lg">
                  <div className="flex justify-between items-center mb-2">
                    <h4 className="font-bold text-gray-800">{mealTitle}</h4>
                    <span className="text-sm text-gray-500">Hedef: {mealInfo.targetCalories} kcal</span>
                  </div>
                  <p className="text-gray-700">{mealInfo.description}</p>
                </div>
              );
            })}
          </div>
          
          <div className="mt-6 text-center">
            <button 
              className="bg-diet-green text-white py-2 px-6 rounded-lg hover:bg-green-600 transition"
              onClick={() => generateDietPlan(userInfo)}
            >
              Farkl neriler Al
            </button>
            <p className="text-sm text-gray-500 mt-2">
              Beenmediiniz yemekler iin alternatif neriler almak iin tklayn.
            </p>
          </div>
        </div>
      )}
      
      {activeTab === 'tips' && (
        <div>
          <h3 className="font-bold text-diet-green text-xl mb-4">Beslenme nerileri</h3>
          
          <div className="space-y-6">
            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-bold text-gray-800 mb-2">Protein Kaynaklar</h4>
              <p className="text-gray-700 mb-2">Gnlk protein ihtiyacnz: <span className="font-bold">{dietPlan.macros.protein.grams}g</span></p>
              <ul className="list-disc list-inside space-y-1">
                <li>Tavuk gs (100g) - 24g protein</li>
                <li>Yumurta (1 adet) - 6g protein</li>
                <li>Yourt (1 kase) - 10g protein</li>
                <li>Krmz et (100g) - 20g protein</li>
                <li>Balk (100g) - 22g protein</li>
                <li>Mercimek (100g pimi) - 9g protein</li>
                <li>Peynir (30g) - 7g protein</li>
              </ul>
            </div>
            
            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-bold text-gray-800 mb-2">Salkl Karbonhidrat Kaynaklar</h4>
              <p className="text-gray-700 mb-2">Gnlk karbonhidrat ihtiyacnz: <span className="font-bold">{dietPlan.macros.carbs.grams}g</span></p>
              <ul className="list-disc list-inside space-y-1">
                <li>Tam tahll ekmek (1 dilim) - 15g karbonhidrat</li>
                <li>Yulaf ezmesi (1/2 kase) - 27g karbonhidrat</li>
                <li>Bulgur (1/2 kase pimi) - 17g karbonhidrat</li>
                <li>Kahverengi pirin (1/2 kase pimi) - 22g karbonhidrat</li>
                <li>Patates (1 orta boy) - 30g karbonhidrat</li>
                <li>Muz (1 adet) - 27g karbonhidrat</li>
                <li>Kuru baklagiller (100g pimi) - 20g karbonhidrat</li>
              </ul>
            </div>
            
            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-bold text-gray-800 mb-2">Salkl Ya Kaynaklar</h4>
              <p className="text-gray-700 mb-2">Gnlk ya ihtiyacnz: <span className="font-bold">{dietPlan.macros.fat.grams}g</span></p>
              <ul className="list-disc list-inside space-y-1">
                <li>Zeytinya (1 yemek ka) - 14g ya</li>
                <li>Avokado (1/2 adet) - 15g ya</li>
                <li>Ceviz (30g) - 19g ya</li>
                <li>Badem (30g) - 15g ya</li>
                <li>Fndk (30g) - 18g ya</li>
                <li>Keten tohumu (1 yemek ka) - 4g ya</li>
                <li>Yumurta sars (1 adet) - 5g ya</li>
              </ul>
            </div>
            
            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-bold text-gray-800 mb-2">Darda Yemek Yerken</h4>
              <ul className="list-disc list-inside space-y-1">
                <li>Izgara et, tavuk veya balk tercih edin.</li>
                <li>Salata veya sebze garnitr isteyin.</li>
                <li>Sulu ve yal soslardan kann.</li>
                <li>Tam tahll ekmek/bulgur/pirin gibi seenekleri tercih edin.</li>
                <li>Tatl yerine meyve veya sade Trk kahvesi tercih edin.</li>
                <li>Alkoll iecekler yerine su, ayran veya ekersiz iecekler iin.</li>
              </ul>
            </div>
            
            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-bold text-gray-800 mb-2">Alternatif Ara n Fikirleri</h4>
              <ul className="list-disc list-inside space-y-1">
                <li>Yarm yal lor peyniri (4 yemek ka) + salatalk/domates</li>
                <li>Yarm yal ayran (1 bardak) + tam tahll kraker (3-4 adet)</li>
                <li>Ceviz, badem, fndk karm (1 avu)</li>
                <li>Mevsim meyveleri (1 porsiyon)</li>
                <li>Ev yapm kefir (1 su barda) + tarn</li>
                <li>Tam tahll ekmek (1 dilim) + 1 tatl ka pekmez</li>
                <li>Labne peynir (2 yemek ka) + salatalk</li>
              </ul>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default DietPlanGenerator; 