import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import '../styles/LandingPage.css';

const LandingPage = () => {
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    // Sayfa yklenme animasyonu
    setIsLoaded(true);

    // Scroll efektleri
    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('show');
        }
      });
    }, { threshold: 0.1 });

    const hiddenElements = document.querySelectorAll('.fade-in');
    hiddenElements.forEach((el) => observer.observe(el));

    // Floating food animasyonlar
    const foodElements = document.querySelectorAll('.floating-food');
    foodElements.forEach(food => {
      food.style.animationDelay = `${Math.random() * 5}s`;
    });

    return () => {
      hiddenElements.forEach((el) => observer.unobserve(el));
    };
  }, []);

  return (
    <div className={`landing-container ${isLoaded ? 'loaded' : ''}`}>
      {/* Hareketli arka plan elementleri */}
      <div className="animated-background">
        <div className="shape circle"></div>
        <div className="shape square"></div>
        <div className="shape triangle"></div>
        <div className="shape rectangle"></div>
      </div>

      {/* Floating food emojileri */}
      <div className="floating-foods-container">
        <div className="floating-food" style={{ top: '10%', left: '5%' }}></div>
        <div className="floating-food" style={{ top: '25%', right: '8%' }}></div>
        <div className="floating-food" style={{ top: '60%', left: '12%' }}></div>
        <div className="floating-food" style={{ top: '75%', right: '15%' }}></div>
        <div className="floating-food" style={{ top: '40%', left: '80%' }}></div>
        <div className="floating-food" style={{ top: '85%', left: '40%' }}></div>
        <div className="floating-food" style={{ top: '15%', left: '50%' }}></div>
        <div className="floating-food" style={{ top: '50%', left: '25%' }}></div>
        <div className="floating-food" style={{ top: '30%', left: '70%' }}></div>
        <div className="floating-food" style={{ top: '70%', left: '60%' }}></div>
      </div>

      {/* Hero Section */}
      <section className="hero-section">
        <div className="hero-overlay"></div>
        <div className="hero-content">
          <div className="logo-animation">
            <div className="logo-circle">
              <span className="logo-icon"></span>
            </div>
          </div>
          <h1 className="main-title">Diyet Dostu</h1>
          <div className="typing-container">
            <h2 className="subtitle">Kiisel salk ve <span className="typewriter">beslenme asistannz</span></h2>
          </div>
          <p className="description fade-in">
            Yapay zeka ile glendirilmi beslenme danmannz ile daha salkl bir yaam iin adm atn.
            Anlk neriler, kiiselletirilmi beslenme planlar ve 7/24 destek!
          </p>
          <div className="button-container">
            <Link to="/register" className="cta-button signup-button fade-in pulse">Hemen Bala</Link>
            <Link to="/login" className="cta-button login-button fade-in">Giri Yap</Link>
          </div>
        </div>
        <div className="hero-image fade-in">
          <div className="app-preview">
            <div className="web-app-mockup">
              <div className="web-app-header">
                <div className="app-logo">
                  <span className="app-logo-icon"></span>
                  <span className="app-logo-text">Diyet Dostu</span>
                </div>
                <div className="web-app-menu">
                  <div className="menu-item">Ana Sayfa</div>
                  <div className="menu-item active">Sohbet</div>
                  <div className="menu-item">Profil</div>
                  <div className="menu-item">Tarifler</div>
                </div>
              </div>
              <div className="web-app-content">
                <div className="chat-container">
                  <div className="chat-bubble user">10 kilo vermek istiyorum, salkl bir ekilde nasl yapabilirim?</div>
                  <div className="chat-bubble assistant">
                    <div className="assistant-header">
                      <div className="assistant-avatar"></div>
                      <div className="assistant-name">Diyet Dostu</div>
                    </div>
                    Merhaba!  10 kilo verme hedefinize salkl bir ekilde ulamak iin u admlar izleyebilirsiniz:
                    <ul>
                      <li>Gnlk 500-600 kalori a oluturun (sert diyet yapmadan)</li>
                      <li>Protein arlkl beslenin (yasz et, balk, baklagiller)</li>
                      <li>lenmi eker ve basit karbonhidratlar azaltn</li>
                      <li>Gnde en az 2-3 litre su iin</li>
                      <li>Haftada 3-4 gn hem kardiyo hem de kuvvet antrenman yapn</li>
                      <li>Uyku dzeninize dikkat edin, her gece 7-8 saat uyumaya aln</li>
                    </ul>
                    <p>Hzl sonu almak yerine, kalc ve salkl alkanlklar edinmeye odaklann. Bylece verdiiniz kilolar geri almaz, salkl bir vcuda kavuursunuz.</p>
                  </div>
                  <div className="chat-bubble user">Bu hzda ka ayda 10 kilo verebilirim?</div>
                  <div className="chat-bubble assistant typing">
                    <div className="typing-indicator">
                      <span></span>
                      <span></span>
                      <span></span>
                    </div>
                  </div>
                </div>
                <div className="chat-input-area">
                  <input type="text" placeholder="Bir mesaj yazn..." className="chat-input" />
                  <button className="send-button">Gnder</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section className="features-section">
        <div className="section-header">
          <h2 className="section-title fade-in">Diyet Dostu ile Neler Yapabilirsiniz?</h2>
          <div className="section-underline"></div>
        </div>
        
        <div className="features-grid">
          <div className="feature-card fade-in">
            <div className="feature-icon-container">
              <div className="feature-icon"></div>
            </div>
            <h3>Kiiselletirilmi Beslenme Plan</h3>
            <p>Ya, boy, kilo ve hedeflerinize gre zel olarak hazrlanm beslenme nerileri aln.</p>
          </div>
          
          <div className="feature-card fade-in">
            <div className="feature-icon-container">
              <div className="feature-icon"></div>
            </div>
            <h3>Egzersiz Programlar</h3>
            <p>Fiziksel durumunuza uygun egzersiz planlar ile formda kaln ve hedeflerinize ulan.</p>
          </div>
          
          <div className="feature-card fade-in">
            <div className="feature-icon-container">
              <div className="feature-icon"></div>
            </div>
            <h3>AI Destekli Sohbet</h3>
            <p>Beslenme ve salk konularnda sorularnz sorun, annda yant aln.</p>
          </div>
          
          <div className="feature-card fade-in">
            <div className="feature-icon-container">
              <div className="feature-icon"></div>
            </div>
            <h3>lerleme Takibi</h3>
            <p>Kilo ve llerinizi kaydedin, zaman iindeki deiimi grafiklerle grn.</p>
          </div>

          <div className="feature-card fade-in">
            <div className="feature-icon-container">
              <div className="feature-icon"></div>
            </div>
            <h3>Yemek Tarifleri</h3>
            <p>Salkl ve lezzetli tariflerle beslenme rutininizi keyifli hale getirin.</p>
          </div>

          <div className="feature-card fade-in">
            <div className="feature-icon-container">
              <div className="feature-icon"></div>
            </div>
            <h3>Hatrlatclar</h3>
            <p>Su ime, n ve egzersiz hatrlatclar ile hedeflerinizden amayn.</p>
          </div>
        </div>
      </section>

      {/* How it Works Section */}
      <section className="how-it-works-section">
        <div className="section-header">
          <h2 className="section-title fade-in">Nasl alr?</h2>
          <div className="section-underline"></div>
        </div>
        
        <div className="steps-container">
          <div className="step-card fade-in">
            <div className="step-number-container">
              <div className="step-number">1</div>
            </div>
            <h3>ye Olun</h3>
            <p>Basit bilgilerinizle hzlca kayt olun ve tercihleri belirleyin.</p>
          </div>
          
          <div className="connector fade-in"></div>
          
          <div className="step-card fade-in">
            <div className="step-number-container">
              <div className="step-number">2</div>
            </div>
            <h3>Profilinizi Tamamlayn</h3>
            <p>Hedeflerinizi, boy-kilo bilgilerinizi ve beslenme tercihlerinizi girin.</p>
          </div>
          
          <div className="connector fade-in"></div>
          
          <div className="step-card fade-in">
            <div className="step-number-container">
              <div className="step-number">3</div>
            </div>
            <h3>Yapay Zeka Asistannzla Sohbet Edin</h3>
            <p>Sorularnz sorun, neriler aln ve salkl yaamnza balayn.</p>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="cta-section">
        <div className="cta-overlay"></div>
        <div className="cta-content fade-in">
          <h2>Salkl Yaama lk Adm Atn</h2>
          <p>cretsiz ye olun ve yapay zeka destekli beslenme asistannzla tann.</p>
          <Link to="/register" className="cta-button signup-button pulse">Hemen Bala</Link>
        </div>
      </section>

      {/* Footer */}
      <footer className="landing-footer">
        <div className="footer-content">
          <div className="footer-logo">
            <div className="logo-icon-small"></div>
            <span>Diyet Dostu</span>
          </div>
          <p> 2025 Tm haklar sakldr</p>
        </div>
      </footer>
    </div>
  );
};

export default LandingPage; 