import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import Login from './components/Login';
import Register from './components/Register';
import Chat from './components/Chat';
import Profile from './components/Profile';
import Header from './components/Header';
import LandingPage from './components/LandingPage';
import axios from 'axios';

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  
  useEffect(() => {
    // Token kontrol
    const checkAuth = async () => {
      setIsLoading(true);
      const token = localStorage.getItem('token');
      
      if (token) {
        try {
          // Token' API isteine ekle
          axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
          
          // Kimlik dorulamas baarl
          console.log('Kimlik dorulamas baarl, token:', token.substring(0, 15) + '...');
          setIsAuthenticated(true);
          
          // Test token ise, normal API arlar yerine simlasyon yapacaz
          if (token.startsWith('test_token_')) {
            console.log('Test modu tespit edildi - normal API arlar atlanacak');
          }
        } catch (error) {
          console.error('Token geersiz:', error);
          localStorage.removeItem('token');
          localStorage.removeItem('userId');
          setIsAuthenticated(false);
        }
      } else {
        console.log('Token bulunamad, oturum almam');
        setIsAuthenticated(false);
      }
      
      setIsLoading(false);
    };
    
    checkAuth();
  }, []);
  
  const handleLogin = () => {
    console.log('Login baarl, durum gncelleniyor');
    setIsAuthenticated(true);
    // Axios ile yaplan tm isteklere otomatik token ekle
    const token = localStorage.getItem('token');
    if (token) {
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      console.log('Axios iin header ayarland');
    }
  };
  
  const handleLogout = () => {
    console.log('k yaplyor...');
    localStorage.removeItem('token');
    localStorage.removeItem('userId');
    // Axios headers'dan token' kaldr
    delete axios.defaults.headers.common['Authorization'];
    setIsAuthenticated(false);
  };

  // Sayfa yklenirken loading gster
  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <svg className="animate-spin h-10 w-10 mx-auto text-diet-green" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
          </svg>
          <p className="mt-3 text-gray-600">Ykleniyor...</p>
        </div>
      </div>
    );
  }

  return (
    <Router>
      <div className="min-h-screen flex flex-col">
        {isAuthenticated && <Header isAuthenticated={isAuthenticated} onLogout={handleLogout} />}
        <div className={isAuthenticated ? "flex-grow container mx-auto px-4 py-8" : "flex-grow"}>
          <Routes>
            <Route path="/login" element={
              isAuthenticated ? <Navigate to="/chat" replace /> : <Login onLogin={handleLogin} />
            } />
            <Route path="/register" element={
              isAuthenticated ? <Navigate to="/chat" replace /> : <Register onLogin={handleLogin} />
            } />
            <Route path="/chat" element={
              isAuthenticated ? <Chat /> : <Navigate to="/login" replace />
            } />
            <Route path="/profile" element={
              isAuthenticated ? <Profile /> : <Navigate to="/login" replace />
            } />
            <Route path="/" element={
              isAuthenticated ? <Navigate to="/chat" replace /> : <LandingPage />
            } />
            {/* Ynlendirici tarafndan eletirilmeyen herhangi bir yolu chat veya login'e ynlendirin */}
            <Route path="*" element={<Navigate to={isAuthenticated ? "/chat" : "/"} replace />} />
          </Routes>
        </div>
        {isAuthenticated && (
          <footer className="bg-white py-4 text-center shadow-inner">
            <p className="text-gray-600"> 2025 Diyet Dostu  - Salkl yaam asistannz</p>
          </footer>
        )}
      </div>
    </Router>
  );
}

export default App; 