import React, { useState } from 'react';
import axios from 'axios';

const UserInfoForm = ({ onSubmit, onCancel }) => {
  const [formData, setFormData] = useState({
    age: '',
    gender: 'erkek',
    weight: '',
    height: '',
    activityLevel: 'sedanter',
    healthProblems: '',
    medications: '',
    dietHistory: '',
    mealCount: '3',
    allergies: '',
    foodPreferences: '',
    dietGoal: 'kilo_verme',
    targetWeight: '',
    weeklyExerciseHours: '0'
  });

  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
    
    // Hata mesajn temizle
    if (errors[name]) {
      setErrors({
        ...errors,
        [name]: ''
      });
    }
  };

  const validateForm = () => {
    const newErrors = {};

    // Zorunlu alanlar kontrol et
    if (!formData.age) newErrors.age = 'Ya alan zorunludur';
    else if (formData.age < 12 || formData.age > 120) newErrors.age = 'Ltfen geerli bir ya giriniz (12-120)';
    
    if (!formData.weight) newErrors.weight = 'Kilo alan zorunludur';
    else if (formData.weight < 30 || formData.weight > 300) newErrors.weight = 'Ltfen geerli bir kilo giriniz (30-300)';
    
    if (!formData.height) newErrors.height = 'Boy alan zorunludur';
    else if (formData.height < 100 || formData.height > 250) newErrors.height = 'Ltfen geerli bir boy giriniz (100-250)';
    
    if (formData.dietGoal === 'kilo_verme' && !formData.targetWeight) {
      newErrors.targetWeight = 'Hedef kilo alan zorunludur';
    } else if (formData.targetWeight && (formData.targetWeight < 30 || formData.targetWeight > 300)) {
      newErrors.targetWeight = 'Ltfen geerli bir hedef kilo giriniz (30-300)';
    }

    return newErrors;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // Form dorulamas yap
    const newErrors = validateForm();
    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      // API'ye kullanc bilgilerini gnderme ilemi
      // Bu noktada gerek API entegrasyonu yaplmaldr
      // imdilik onSubmit fonksiyonunu dorudan aryoruz
      
      const token = localStorage.getItem('token');
      if (token) {
        await axios.post('http://localhost:5000/api/user-info', formData, {
          headers: { Authorization: `Bearer ${token}` }
        });
      }
      
      onSubmit(formData);
    } catch (error) {
      console.error('Kullanc bilgileri kaydedilirken hata:', error);
      setErrors({
        submit: 'Bilgiler kaydedilirken bir hata olutu. Ltfen daha sonra tekrar deneyin.'
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="bg-white rounded-lg shadow-xl p-6 max-w-2xl mx-auto">
      <div className="mb-6 text-center">
        <h2 className="text-2xl font-bold text-diet-green">Kiisel Bilgi Formu</h2>
        <p className="text-gray-600 mt-2">
          Daha kiiselletirilmi bir beslenme program iin ltfen aadaki bilgileri doldurun.
        </p>
      </div>
      
      {errors.submit && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
          {errors.submit}
        </div>
      )}
      
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {/* Temel Bilgiler */}
          <div>
            <label className="block text-gray-700 mb-1" htmlFor="age">
              Yanz <span className="text-red-500">*</span>
            </label>
            <input
              type="number"
              id="age"
              name="age"
              value={formData.age}
              onChange={handleChange}
              className={`w-full p-2 border rounded-md ${errors.age ? 'border-red-500' : 'border-gray-300'}`}
              placeholder="rn: 35"
            />
            {errors.age && <p className="mt-1 text-red-500 text-sm">{errors.age}</p>}
          </div>
          
          <div>
            <label className="block text-gray-700 mb-1" htmlFor="gender">
              Cinsiyet <span className="text-red-500">*</span>
            </label>
            <select
              id="gender"
              name="gender"
              value={formData.gender}
              onChange={handleChange}
              className="w-full p-2 border border-gray-300 rounded-md"
            >
              <option value="erkek">Erkek</option>
              <option value="kadn">Kadn</option>
              <option value="dier">Dier</option>
            </select>
          </div>
          
          <div>
            <label className="block text-gray-700 mb-1" htmlFor="weight">
              Kilonuz (kg) <span className="text-red-500">*</span>
            </label>
            <input
              type="number"
              id="weight"
              name="weight"
              value={formData.weight}
              onChange={handleChange}
              className={`w-full p-2 border rounded-md ${errors.weight ? 'border-red-500' : 'border-gray-300'}`}
              placeholder="rn: 75"
            />
            {errors.weight && <p className="mt-1 text-red-500 text-sm">{errors.weight}</p>}
          </div>
          
          <div>
            <label className="block text-gray-700 mb-1" htmlFor="height">
              Boyunuz (cm) <span className="text-red-500">*</span>
            </label>
            <input
              type="number"
              id="height"
              name="height"
              value={formData.height}
              onChange={handleChange}
              className={`w-full p-2 border rounded-md ${errors.height ? 'border-red-500' : 'border-gray-300'}`}
              placeholder="rn: 175"
            />
            {errors.height && <p className="mt-1 text-red-500 text-sm">{errors.height}</p>}
          </div>
          
          <div>
            <label className="block text-gray-700 mb-1" htmlFor="activityLevel">
              Fiziksel Aktivite Dzeyi <span className="text-red-500">*</span>
            </label>
            <select
              id="activityLevel"
              name="activityLevel"
              value={formData.activityLevel}
              onChange={handleChange}
              className="w-full p-2 border border-gray-300 rounded-md"
            >
              <option value="sedanter">Sedanter (Masa ba alan/Az hareket)</option>
              <option value="hafif_aktif">Hafif Aktif (Haftada 1-3 gn egzersiz)</option>
              <option value="orta_aktif">Orta Aktif (Haftada 3-5 gn egzersiz)</option>
              <option value="cok_aktif">ok Aktif (Haftada 6-7 gn egzersiz)</option>
              <option value="ekstra_aktif">Ekstra Aktif (Gnde 2 kez egzersiz/Fiziksel i)</option>
            </select>
          </div>
          
          <div>
            <label className="block text-gray-700 mb-1" htmlFor="dietGoal">
              Beslenme Hedefiniz <span className="text-red-500">*</span>
            </label>
            <select
              id="dietGoal"
              name="dietGoal"
              value={formData.dietGoal}
              onChange={handleChange}
              className="w-full p-2 border border-gray-300 rounded-md"
            >
              <option value="kilo_verme">Kilo Vermek</option>
              <option value="kilo_alma">Kilo Almak</option>
              <option value="kilo_koruma">Mevcut Kiloyu Korumak</option>
              <option value="kas_kazanma">Kas Ktlesi Kazanmak</option>
              <option value="saglikli_beslenme">Genel Salkl Beslenme</option>
            </select>
          </div>
        </div>
        
        {formData.dietGoal === 'kilo_verme' && (
          <div>
            <label className="block text-gray-700 mb-1" htmlFor="targetWeight">
              Hedef Kilonuz (kg) <span className="text-red-500">*</span>
            </label>
            <input
              type="number"
              id="targetWeight"
              name="targetWeight"
              value={formData.targetWeight}
              onChange={handleChange}
              className={`w-full p-2 border rounded-md ${errors.targetWeight ? 'border-red-500' : 'border-gray-300'}`}
              placeholder="rn: 65"
            />
            {errors.targetWeight && <p className="mt-1 text-red-500 text-sm">{errors.targetWeight}</p>}
          </div>
        )}
        
        <div>
          <label className="block text-gray-700 mb-1" htmlFor="mealCount">
            Gnde ka n yemek tercih edersiniz?
          </label>
          <select
            id="mealCount"
            name="mealCount"
            value={formData.mealCount}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 rounded-md"
          >
            <option value="3">3 Ana n</option>
            <option value="4">3 Ana n + 1 Ara n</option>
            <option value="5">3 Ana n + 2 Ara n</option>
            <option value="6">3 Ana n + 3 Ara n</option>
          </select>
        </div>
        
        <div>
          <label className="block text-gray-700 mb-1" htmlFor="allergies">
            Besin Alerjileriniz
          </label>
          <input
            type="text"
            id="allergies"
            name="allergies"
            value={formData.allergies}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 rounded-md"
            placeholder="Varsa besin alerjilerinizi yazn (rn: st, fndk)"
          />
        </div>
        
        <div>
          <label className="block text-gray-700 mb-1" htmlFor="foodPreferences">
            Yemek Tercihleri/Sevmedikleriniz
          </label>
          <input
            type="text"
            id="foodPreferences"
            name="foodPreferences"
            value={formData.foodPreferences}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 rounded-md"
            placeholder="Sevmediiniz veya tercih ettiiniz besinleri yazn"
          />
        </div>
        
        <div>
          <label className="block text-gray-700 mb-1" htmlFor="healthProblems">
            Salk Sorunlarnz
          </label>
          <textarea
            id="healthProblems"
            name="healthProblems"
            value={formData.healthProblems}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 rounded-md"
            placeholder="Varsa salk sorunlarnz yazn (Diyabet, tansiyon, kolesterol vb.)"
            rows="2"
          ></textarea>
        </div>
        
        <div>
          <label className="block text-gray-700 mb-1" htmlFor="medications">
            Kullandnz lalar
          </label>
          <textarea
            id="medications"
            name="medications"
            value={formData.medications}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 rounded-md"
            placeholder="Varsa kullandnz ilalar yazn"
            rows="2"
          ></textarea>
        </div>
        
        <div>
          <label className="block text-gray-700 mb-1" htmlFor="dietHistory">
            Daha nce Denediiniz Diyetler
          </label>
          <textarea
            id="dietHistory"
            name="dietHistory"
            value={formData.dietHistory}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 rounded-md"
            placeholder="Daha nce uyguladnz diyetler ve sonular"
            rows="2"
          ></textarea>
        </div>
        
        <div className="text-right mt-6 flex justify-between">
          <button
            type="button"
            onClick={onCancel}
            className="bg-gray-200 text-gray-700 py-2 px-6 rounded-lg hover:bg-gray-300 transition"
          >
            ptal
          </button>
          <button
            type="submit"
            disabled={isSubmitting}
            className="bg-diet-green text-white py-2 px-6 rounded-lg hover:bg-green-600 transition flex items-center"
          >
            {isSubmitting && (
              <svg className="animate-spin -ml-1 mr-2 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
            )}
            {isSubmitting ? 'Kaydediliyor...' : 'Bilgilerimi Kaydet'}
          </button>
        </div>
      </form>
    </div>
  );
};

export default UserInfoForm; 