import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import ChatBubble from './ChatBubble';
import UserInfoForm from './UserInfoForm';
import DietPlanGenerator from './DietPlanGenerator';

const Chat = () => {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [imageFile, setImageFile] = useState(null);
  const [imagePreview, setImagePreview] = useState(null);
  const [chatList, setChatList] = useState([]);
  const [activeChatId, setActiveChatId] = useState(null);
  const [sidebarOpen, setSidebarOpen] = useState(true);
  const [showUserInfoForm, setShowUserInfoForm] = useState(false);
  const [showDietPlan, setShowDietPlan] = useState(false);
  const [userInfo, setUserInfo] = useState(null);
  const messagesEndRef = useRef(null);
  const textareaRef = useRef(null);

  useEffect(() => {
    fetchChatList();
    checkUserInfo();
  }, []);

  useEffect(() => {
    if (activeChatId) {
      fetchChatHistory();
    }
  }, [activeChatId]);

  useEffect(() => {
    scrollToBottom();
  }, [messages, showUserInfoForm, showDietPlan]);

  const checkUserInfo = async () => {
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamad');
        return;
      }
      
      const response = await axios.get('/api/user-info', {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      if (response.data.userInfo) {
        setUserInfo(response.data.userInfo);
      } else {
        // Kullanc bilgisi yoksa formu gster
        // Gerek uygulamada sohbet balattktan sonra gster
        // imdilik yorum satr olarak brakyoruz
        // setShowUserInfoForm(true);
      }
    } catch (error) {
      console.error('Kullanc bilgisi kontrol srasnda hata:', error);
    }
  };

  const fetchChatList = async () => {
    try {
      setIsLoading(true);
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamad');
        setIsLoading(false);
        return;
      }
      
      const response = await axios.get('/api/chat-list', {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      // Sohbet listesini ayarla
      setChatList(response.data);
      
      // lk sohbeti aktif olarak iaretle (eer liste bo deilse)
      if (response.data.length > 0 && !activeChatId) {
        setActiveChatId(response.data[0].id);
        fetchChatHistory(response.data[0].id);
      } else if (response.data.length === 0) {
        // Sohbet listesi bosa yeni bir sohbet olutur
        createNewChat();
      }
    } catch (error) {
      console.error('Sohbet listesi alnrken hata:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const fetchChatHistory = async (chatId) => {
    try {
      setIsLoading(true);
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamad');
        setIsLoading(false);
        return;
      }
      
      const response = await axios.get(`/api/chat-history/${chatId}`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      // Baarl yantta mesajlar gncelle
      if (response.data.chatHistory && Array.isArray(response.data.chatHistory)) {
        setMessages(response.data.chatHistory);
      } else {
        // Bo mesaj listesi
        setMessages([]);
      }
    } catch (error) {
      console.error('Sohbet gemii alnrken hata:', error);
      setMessages([]);
    } finally {
      setIsLoading(false);
    }
  };

  const createNewChat = () => {
    try {
      setIsLoading(true);
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamad');
        setIsLoading(false);
        return;
      }
      
      // Yeni sohbet olutur
      axios.post('/api/create-chat', {}, {
        headers: { Authorization: `Bearer ${token}` }
      })
      .then(response => {
        if (response.data && response.data.id) {
          // Yeni sohbeti listeye ekle
          const newChat = {
            id: response.data.id,
            title: response.data.title || 'Yeni Sohbet',
            lastMessage: new Date().toISOString()
          };
          
          setChatList(prevList => [...prevList, newChat]);
          setActiveChatId(response.data.id);
          
          // Yeni sohbetin gemiini ykle (bo olacak)
          setMessages([{
            role: 'assistant',
            content: `Merhaba!  Diyet Dostu'na ho geldin!

Ben senin kiisel beslenme ve spor asistannm. Bana istediin konuda soru sorabilirsin.

Trk mutfana uygun, kolay ve salkl tarifler sunarak diyet hedeflerine ulamana yardmc olacam.

te sana yardmc olabileceim konular:
 Trk mutfana uygun dk kalorili/yksek proteinli tarifler
 Kiiselletirilmi egzersiz planlar
 Yemek fotoraflarn analiz ederek besin deeri tahmini
 Haftalk beslenme program oluturma
 Kilo takibi ve geliim analizi

Sormak istediin bir ey var m? Birlikte salkl bir yaam yolculuuna balayalm! `
          }]);
        }
      })
      .catch(error => {
        console.error('Yeni sohbet oluturulurken hata:', error);
      })
      .finally(() => {
        setIsLoading(false);
      });
    } catch (error) {
      console.error('Yeni sohbet oluturulurken hata:', error);
      setIsLoading(false);
    }
  };

  const deleteChat = async (chatId) => {
    if (!window.confirm('Bu sohbeti silmek istediinizden emin misiniz?')) {
      return;
    }
    
    try {
      setIsLoading(true);
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamad');
        setIsLoading(false);
        return;
      }
      
      // API'ya istek gnder
      const response = await axios.delete(`/api/delete-chat/${chatId}`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      // Baarl yantta UI' gncelle
      if (response.data.success) {
        // Sohbet listesinden sil
        setChatList(prevList => prevList.filter(chat => chat.id !== chatId));
        
        // Eer aktif sohbet siliniyorsa baka bir sohbete ge
        if (chatId === activeChatId) {
          const remainingChats = chatList.filter(chat => chat.id !== chatId);
          const nextChat = remainingChats.length > 0 ? remainingChats[0] : null;
          
          if (nextChat) {
            setActiveChatId(nextChat.id);
            // Bu sohbetin gemiini ykle
            fetchChatHistory(nextChat.id);
          } else {
            // Hi sohbet kalmadysa yeni bir sohbet olutur
            createNewChat();
          }
        }
        
        // Bildirim gster
        alert('Sohbet baaryla silindi.');
      } else {
        alert('Sohbet silinirken bir hata olutu.');
      }
    } catch (error) {
      console.error('Sohbet silinirken hata:', error);
      alert('Sohbet silinirken bir hata olutu. Ltfen tekrar deneyin.');
    } finally {
      setIsLoading(false);
    }
  };

  const clearChatHistory = async () => {
    try {
      if (!window.confirm('Sohbet gemiini temizlemek istediinizden emin misiniz?')) {
        return;
      }
      
      setIsLoading(true);
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamad');
        setIsLoading(false);
        return;
      }
      
      // API'ya istek gnder
      const response = await axios.post('/api/clear-chat', {}, {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      if (response.data.success) {
        // Baarl yantta mesajlar gncelle
        setMessages(response.data.chatHistory || [{
          role: 'assistant',
          content: `Merhaba!  Diyet Dostu'na ho geldin!

Ben senin kiisel beslenme ve spor asistannm. Sohbet gemiin temizlendi ve yeni bir konuma balatld.

Trk mutfana uygun, kolay ve salkl tarifler sunarak diyet hedeflerine ulamana yardmc olacam.

te sana yardmc olabileceim konular:
 Trk mutfana uygun dk kalorili/yksek proteinli tarifler
 Kiiselletirilmi egzersiz planlar
 Yemek fotoraflarn analiz ederek besin deeri tahmini
 Haftalk beslenme program oluturma
 Kilo takibi ve geliim analizi

Sormak istediin bir ey var m? Birlikte salkl bir yaam yolculuuna balayalm! `
        }]);
        
        // Aktif sohbeti gncelle
        const updatedList = chatList.map(chat => {
          if (chat.id === activeChatId) {
            return { ...chat, title: 'Temizlenmi Sohbet' };
          }
          return chat;
        });
        
        setChatList(updatedList);
        
        // Formlar gizle
        setShowUserInfoForm(false);
        setShowDietPlan(false);
        
        // Baar mesaj gster
        alert('Sohbet gemii baaryla temizlendi.');
      } else {
        // Hata mesaj
        alert('Sohbet gemii temizlenirken bir hata olutu.');
      }
    } catch (error) {
      console.error('Sohbet gemii temizlenirken hata:', error);
      // Hata durumunda kullancy bilgilendir
      alert('Sohbet gemii temizlenirken bir hata olutu. Ltfen tekrar deneyin.');
    } finally {
      setIsLoading(false);
    }
  };

  const scrollToBottom = () => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit(e);
    }
  };

  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      setImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const clearImage = () => {
    setImageFile(null);
    setImagePreview(null);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const message = input.trim();
    
    if (!message && !imageFile) return;
    
    try {
      setIsLoading(true);
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamad');
        setMessages((prev) => [...prev, {
          role: 'assistant',
          content: 'Oturum sreniz dolmu grnyor. Ltfen tekrar giri yapn.'
        }]);
        setIsLoading(false);
        return;
      }
      
      // Kullanc mesajn ekle
      const userMessage = {
        role: 'user',
        content: message
      };
      
      setMessages((prev) => [...prev, userMessage]);
      setInput('');
      
      // zel komutlar kontrol et
      if (message.toLowerCase().includes('kiisel bilgilerimi girmek istiyorum') ||
          message.toLowerCase().includes('diyet program oluturmak istiyorum') ||
          message.toLowerCase().includes('bilgilerimi gncellemek istiyorum')) {
        
        setTimeout(() => {
          setShowUserInfoForm(true);
          setIsLoading(false);
        }, 500);
        
        return;
      }
      
      if (message.toLowerCase().includes('diyet planm gster') ||
          message.toLowerCase().includes('beslenme planm gster') ||
          message.toLowerCase().includes('beslenme plan olutur')) {
        
        if (!userInfo) {
          // Kullanc bilgisi yoksa bilgi formunu gster
          setTimeout(() => {
            setMessages((prev) => [...prev, {
              role: 'assistant',
              content: 'Diyet plan oluturmak iin ncelikle kiisel bilgilerinizi girmeniz gerekiyor. Bilgi formunu ayorum...'
            }]);
            
            setTimeout(() => {
              setShowUserInfoForm(true);
              setIsLoading(false);
            }, 500);
          }, 500);
        } else {
          // Kullanc bilgisi varsa diyet plann gster
          setTimeout(() => {
            setMessages((prev) => [...prev, {
              role: 'assistant',
              content: 'te size zel hazrladm beslenme plan:'
            }]);
            
            setTimeout(() => {
              setShowDietPlan(true);
              setIsLoading(false);
            }, 500);
          }, 500);
        }
        
        return;
      }
      
      // Eer grsel varsa, farkl bir endpoint kullanarak grsel analizi yapn
      if (imageFile) {
        const formData = new FormData();
        formData.append('image', imageFile);
        
        try {
          console.log('Grsel analizi istei gnderiliyor...');
          const response = await axios.post(
            '/api/analyze-image',
            formData,
            {
              headers: { 
                Authorization: `Bearer ${token}`,
                'Content-Type': 'multipart/form-data' 
              }
            }
          );
          
          console.log('Grsel analiz yant:', response.data);
          
          // Analiz sonucunu gster (eer socket.io ile otomatik gelmiyorsa)
          if (!response.data.message || response.data.message !== 'Grnt analiz edildi') {
            // Mesaj yant sadece bildirim ieriyorsa, birka saniye sonra sohbet gemiini gncelle
            setTimeout(() => {
              fetchChatHistory(activeChatId);
            }, 1000);
          }
          
          // Grsel nizlemeyi temizle
          clearImage();
        } catch (imgError) {
          console.error('Grsel analiz hatas:', imgError);
          
          // Hata mesajn sohbete ekle
          setMessages((prev) => [...prev, {
            role: 'assistant',
            content: 'Grsel analiz edilirken bir hata olutu. Ltfen daha sonra tekrar deneyin veya baka bir grsel ykleyin.'
          }]);
        }
      } else {
        // Grsel yoksa normal mesaj gnderimi yap
        try {
          console.log('Mesaj gnderiliyor:', message);
          console.log('Aktif sohbet ID:', activeChatId);
          
          const response = await axios.post(
            '/api/chat',
            {
              message,
              chatId: activeChatId
            },
            {
              headers: { Authorization: `Bearer ${token}` }
            }
          );
          
          console.log('API yant:', response.data);
          
          // Asistan yantn ekle (cevap varsa)
          if (response.data.response) {
            const assistantMessage = {
              role: 'assistant',
              content: response.data.response
            };
            
            setMessages((prev) => [...prev, assistantMessage]);
          } else {
            // Yant yoksa, sohbet gemiini gncellemek iin bekle
            setTimeout(() => {
              fetchChatHistory(activeChatId);
            }, 1000);
          }
        } catch (chatError) {
          console.error('Mesaj gnderme hatas:', chatError);
          
          // Hata mesajn sohbete ekle ve daha aklayc hata mesajlar sala
          let errorMessage = 'zgnm, mesajnz gnderilemedi. Ltfen internet balantnz kontrol edin veya daha sonra tekrar deneyin.';
          
          if (chatError.response) {
            // Sunucu yant var, hata kodu ile birlikte
            console.error('Sunucu yant kodu:', chatError.response.status);
            console.error('Sunucu hata mesaj:', chatError.response.data);
            
            if (chatError.response.status === 401 || chatError.response.status === 403) {
              errorMessage = 'Oturum sreniz dolmu grnyor. Ltfen tekrar giri yapn.';
            } else if (chatError.response.status === 404) {
              errorMessage = 'API endpoint bulunamad. Sunucu yaplandrmas kontrol edilmeli.';
            } else if (chatError.response.status === 500) {
              errorMessage = 'Sunucu hatas. Ltfen daha sonra tekrar deneyin.';
            }
          } else if (chatError.request) {
            // stek yapld ama yant alnamad
            console.error('Yant alnamad:', chatError.request);
            errorMessage = 'Sunucuya balanlamyor. Ltfen sunucunun altndan emin olun.';
          }
          
          setMessages((prev) => [...prev, {
            role: 'assistant',
            content: errorMessage
          }]);
        }
      }
    } catch (error) {
      console.error('Mesaj gnderilirken genel hata:', error);
      
      // Hata mesajn sohbete ekleyin
      const errorMessage = {
        role: 'assistant',
        content: 'zgnm, bir hata olutu. Ltfen daha sonra tekrar deneyin.'
      };
      
      setMessages((prev) => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
      setTimeout(scrollToBottom, 100);
    }
  };

  const handleUserInfoSubmit = (formData) => {
    // Kullanc bilgilerini kaydet
    setUserInfo(formData);
    setShowUserInfoForm(false);
    
    // Kullanc bilgileri alndnda diyet plann gster
    setTimeout(() => {
      setMessages((prev) => [...prev, {
        role: 'assistant',
        content: `Teekkrler! Bilgileriniz kaydedildi. 

Size zel bir beslenme plan hazrlyorum. Bu plan, hedeflerinize, tercihlerinize ve ihtiyalarnza gre zelletirilmitir.

te size zel beslenme planm:`
      }]);
      
      setTimeout(() => {
        setShowDietPlan(true);
      }, 1000);
    }, 500);
  };

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
  };

  // Eksik olduu iin not silme fonksiyonunu ekliyorum
  const deleteNote = async (noteId) => {
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamad');
        return;
      }
      
      const response = await axios.delete(`/api/delete-note/${noteId}`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      if (response.data.success) {
        // Notu listeden kaldr
        setMessages(messages.filter(msg => {
          // Not ID'si mesaj iinde tanmlanmsa (data-note-id gibi bir zellik ile)
          return !(msg.type === 'note' && msg.noteId === noteId);
        }));
        
        // Bildirim gster
        alert('Not baaryla silindi');
      } else {
        alert('Not silinirken bir hata olutu');
      }
    } catch (error) {
      console.error('Not silinirken hata:', error);
      alert('Not silinirken bir hata olutu. Ltfen tekrar deneyin.');
    }
  };

  return (
    <div className="flex h-[calc(100vh-64px)]">
      {/* Sol Kenar ubuu - Sohbet Listesi */}
      <div className={`chat-sidebar ${sidebarOpen ? 'open' : 'closed'}`}>
        <div className="p-4 border-b flex justify-between items-center">
          <h3 className="font-semibold text-gray-700">Sohbetlerim</h3>
          <button 
            onClick={createNewChat}
            className="bg-diet-green text-white p-2 rounded-lg hover:bg-green-600 transition-colors"
            title="Yeni sohbet balat"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
            </svg>
          </button>
        </div>
        
        <div className="overflow-y-auto h-full">
          {chatList.map((chat) => (
            <div 
              key={chat.id}
              className={`p-3 border-b cursor-pointer hover:bg-gray-100 flex justify-between items-center ${chat.id === activeChatId ? 'bg-diet-green bg-opacity-10 border-l-4 border-diet-green' : ''}`}
            >
              <div className="flex-1" onClick={() => setActiveChatId(chat.id)}>
                <p className="font-medium text-gray-800">{chat.title}</p>
                <p className="text-xs text-gray-500">{chat.date}</p>
              </div>
              {chat.id !== 'current' && (
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    deleteChat(chat.id);
                  }}
                  className="text-gray-400 hover:text-red-500 p-1"
                  title="Sohbeti sil"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                  </svg>
                </button>
              )}
            </div>
          ))}
        </div>
      </div>
      
      {/* Sohbet Blm */}
      <div className="flex-1 flex flex-col relative">
        {/* Mobil grnmde kenar ubuu ama/kapama butonu */}
        <button 
          className="sidebar-toggle"
          onClick={toggleSidebar}
          title={sidebarOpen ? "Kenar ubuunu kapat" : "Kenar ubuunu a"}
        >
          {sidebarOpen ? (
            <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 19l-7-7 7-7m8 14l-7-7 7-7" />
            </svg>
          ) : (
            <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 5l7 7-7 7M5 5l7 7-7 7" />
            </svg>
          )}
        </button>
        
        <div className="p-4 bg-diet-green text-white flex justify-between items-center">
          <div className="flex items-center">
            <div className="diyet-dostu-avatar">
              <div className="avatar-icon">
                <svg viewBox="0 0 24 24" fill="#4CAF50" xmlns="http://www.w3.org/2000/svg">
                  <circle cx="12" cy="12" r="10" fill="#e8f5e9" />
                  <path d="M17 8.5C17 6.01 14.99 4 12.5 4C10.01 4 8 6.01 8 8.5C8 11.99 12.5 16.5 12.5 16.5C12.5 16.5 17 11.99 17 8.5ZM12.5 9.5C11.95 9.5 11.5 9.05 11.5 8.5C11.5 7.95 11.95 7.5 12.5 7.5C13.05 7.5 13.5 7.95 13.5 8.5C13.5 9.05 13.05 9.5 12.5 9.5Z" fill="#4CAF50" />
                  <path d="M16 15H9C8.45 15 8 15.45 8 16C8 16.55 8.45 17 9 17H16C16.55 17 17 16.55 17 16C17 15.45 16.55 15 16 15Z" fill="#4CAF50" />
                  <path d="M12 20C16.42 20 20 16.42 20 12C20 7.58 16.42 4 12 4C7.58 4 4 7.58 4 12C4 16.42 7.58 20 12 20ZM12 2C17.52 2 22 6.48 22 12C22 17.52 17.52 22 12 22C6.48 22 2 17.52 2 12C2 6.48 6.48 2 12 2Z" fill="#4CAF50" />
                </svg>
              </div>
            </div>
            <div>
              <h2 className="text-xl font-semibold">Diyet Dostu </h2>
              <p className="text-sm">Salkl yaam asistannz</p>
            </div>
          </div>
          <button 
            onClick={clearChatHistory}
            className="bg-white bg-opacity-20 hover:bg-opacity-30 text-white py-1 px-3 rounded-lg text-sm transition-colors duration-200"
            title="Sohbet gemiini temizle"
          >
            Sohbeti Temizle
          </button>
        </div>
        
        <div className="flex-grow overflow-y-auto p-4 chat-messages">
          {messages.length === 0 ? (
            <div className="flex items-center justify-center h-full">
              <div className="text-center">
                <div className="text-4xl mb-4"></div>
                <p className="text-xl font-medium text-gray-700 mb-2">Diyet Dostu'na Ho Geldin!</p>
                <p className="text-gray-500 mb-4">Beslenme, diyet veya fitness hakknda bir eyler sor.</p>
              </div>
            </div>
          ) : (
            messages.map((msg, index) => (
              <div key={index}>
                {msg.type === 'note' ? (
                  <div className="note-container">
                    <ChatBubble message={msg} />
                    <button 
                      onClick={() => deleteNote(msg.noteId)}
                      className="delete-note-btn"
                      title="Notu sil"
                    >
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                      </svg>
                    </button>
                  </div>
                ) : (
                  <ChatBubble message={msg} />
                )}
              </div>
            ))
          )}
          
          {isLoading && (
            <div className="flex items-center space-x-2 text-gray-500 p-4 bg-gray-100 rounded-lg max-w-[80%] mr-auto my-3 shadow-md loading-container">
              <div className="loader-container">
                <div className="food-icons">
                  <span className="icon apple"></span>
                  <span className="icon broccoli"></span>
                  <span className="icon avocado"></span>
                </div>
                <p className="ml-2 font-medium">Diyet Dostu dnyor...</p>
              </div>
            </div>
          )}
          
          {/* Kullanc bilgi formu */}
          {showUserInfoForm && (
            <div className="my-4 p-2 bg-white rounded-lg shadow-lg">
              <UserInfoForm 
                onSubmit={handleUserInfoSubmit} 
                onCancel={() => setShowUserInfoForm(false)}
              />
            </div>
          )}
          
          {/* Diyet plan */}
          {showDietPlan && userInfo && (
            <div className="my-4 p-2 bg-white rounded-lg shadow-lg">
              <DietPlanGenerator userInfo={userInfo} />
            </div>
          )}
          
          <div ref={messagesEndRef} />
        </div>

        <div className="p-4 border-t">
          <form onSubmit={handleSubmit} className="flex items-end gap-2">
            <div className="flex-1 relative">
              <textarea
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyDown={handleKeyDown}
                ref={textareaRef}
                placeholder="Diyet Dostu'na bir soru sor..."
                className="w-full p-3 pr-12 border border-gray-300 rounded-lg resize-none focus:border-diet-green focus:ring-1 focus:ring-diet-green outline-none min-h-[50px] max-h-[200px]"
                rows="1"
              ></textarea>
              
              <label htmlFor="image-upload" className="absolute right-2 bottom-2 cursor-pointer text-gray-500 hover:text-diet-green p-1">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                </svg>
                <input
                  id="image-upload"
                  type="file"
                  accept="image/*"
                  onChange={handleImageUpload}
                  className="hidden"
                />
              </label>
            </div>
            
            <button
              type="submit"
              disabled={isLoading || (!input.trim() && !imageFile)}
              className={`p-3 rounded-lg ${isLoading || (!input.trim() && !imageFile) ? 'bg-gray-300 cursor-not-allowed' : 'bg-diet-green text-white hover:bg-green-600'} transition-colors`}
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 5l7 7-7 7M5 5l7 7-7 7" />
              </svg>
            </button>
          </form>
          
          {imagePreview && (
            <div className="mt-2 relative">
              <img src={imagePreview} alt="Yklenen grsel" className="h-20 object-contain rounded-md border border-gray-300" />
              <button 
                onClick={clearImage}
                className="absolute top-1 right-1 bg-red-500 text-white rounded-full p-1 hover:bg-red-600 transition-colors"
                title="Grseli kaldr"
              >
                <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Chat; 